/* Breakpoint mixins */
@font-face {
  font-family: Londrina;
  src: url("../../fonts/Londrina/LondrinaSolid-Regular.ttf");
}
@font-face {
  font-family: Londrina-Light;
  src: url("../../fonts/Londrina/LondrinaSolid-Light.ttf");
}
@font-face {
  font-family: Londrina-Black;
  src: url("../../fonts/Londrina/LondrinaSolid-Black.ttf");
}
@font-face {
  font-family: Londrina-Thin;
  src: url("../../fonts/Londrina/LondrinaSolid-Thin.ttf");
}
.fs12 {
  font-size: 0.75rem;
}

.fs14 {
  font-size: 0.875rem;
}

.fs18 {
  font-size: 1.125rem;
}

.fs20 {
  font-size: 1.25rem;
}

.fs24 {
  font-size: 1.5rem;
}

.fs28 {
  font-size: 1.75rem;
}

.fs30 {
  font-size: 1.875rem;
}

.center {
  align-content: center;
  justify-items: center;
  margin: auto;
}

.flex {
  display: flex;
}
.flex > *.full {
  flex: 1;
}
.flex.column {
  flex-direction: column;
}
.flex.justify-center {
  justify-content: center;
}
.flex.align-center {
  align-items: center;
}
.flex.space-between {
  justify-content: space-between;
}
.flex.space-around {
  justify-content: space-around;
}

.main-container {
  max-width: 1400px;
  margin: auto;
  transition: max-width 0.5s;
  padding: 0 2rem;
}
@media (max-width: 768px) {
  .main-container {
    padding: 0 1.5rem;
  }
}
@media (max-width: 480px) {
  .main-container {
    padding: 0 0.5rem;
  }
}
@media (min-width: 1400px) {
  .main-container {
    max-width: 100%;
  }
}

@media (max-width: 1000px) {
  .main-layout {
    max-width: 980px;
  }
}

* {
  box-sizing: border-box;
}

html {
  font-family: "Londrina", cursive;
  letter-spacing: 0.8px;
  color: #fff;
  line-height: normal;
  font-size: 16px;
  scroll-behavior: smooth;
  margin: 0;
  padding: 0;
}
@media screen and (max-width: 1130px) {
  html {
    font-size: 13px;
  }
}

body {
  margin: 0;
  padding: 0;
  background-color: #3c8bdf;
  background-image: url("../../img/bg.svg");
  background-size: 50%;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
}

img {
  width: 100%;
}

p {
  margin: 0;
}

a {
  color: inherit;
  text-decoration: none;
}
a:visited {
  color: inherit;
}

ul {
  margin: 0;
  padding: 0;
}

li {
  list-style-type: none;
}

button {
  cursor: pointer;
}

button:active {
  color: white;
  border-color: white;
}

textarea {
  outline: none;
  resize: none;
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
  font-size: 1.1rem;
}

input,
select {
  padding: 5px;
  padding-left: none;
  border: unset;
  background: unset;
  border-bottom: 1px solid black;
}

pre {
  margin: 0;
}

button {
  border: none;
  font-family: Tahoma, Geneva, Verdana, sans-serif;
}

.regular-btn {
  font-size: 1.25rem;
  background-color: #f04882;
  color: white;
  padding: 8px 20px;
  border-radius: 3px;
  transition: 0.3s background-color;
}
.regular-btn:hover {
  background-color: #ee3072;
}